.sidebar {
  background: #9d9d9d;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 20%;
  height: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.item {
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 0;
  }
}